<template>
  <div>
    <div id="g-recaptcha"></div>
  </div>
</template>
<script>
  import DynamicJS from "@/js/core/dynamicjs";
  import formAPI from "@/js/forms/form";
  export default {
    name: "Recaptcha",
    data() {
      return {
        recaptcha_response_url: "https://www.google.com/recaptcha/api/siteverify?",
      };
    },
    props: {
      // Registered site-key
      site_key: {
        type: String,
        required: false,
        default: () => {
          return "6LcnrZcaAAAAAPKrogYEGUe2rbI5r0lYzaVhwfvU";
        },
      },
      // color for captcha
      theme: {
        type: String,
        required: false,
        default: () => {
          return "light";
        },
      },
      // Size for captcha
      size: {
        type: String,
        required: false,
        default: () => {
          return "normal";
        },
      },
    },
    created() {
      // Added google recaptcha into the head tag
      // https://developers.google.com/recaptcha/docs/display#auto_render
      DynamicJS.downloadJS("https://www.google.com/recaptcha/api.js", { async: true, defer: true });
    },
    mounted() {
      this.render();
    },
    methods: {
      render() {
        let _this = this;
        setTimeout(() => {
          if (!window?.grecaptcha?.render) {
            _this.render();
          } else {
            window.grecaptcha.render(document.getElementById("g-recaptcha"), {
              sitekey: _this.site_key,
              size: _this.size,
              // From recaptcha
              callback: (response) => {
                _this.recaptchaCallback(response);
              },
            });
          }
        }, 100);
      },
      // Received response from recaptcha
      async recaptchaCallback(response) {
        let _this = this;
        formAPI.params = "";
        formAPI.pcors = true;
        formAPI.url = `${_this.recaptcha_response_url}secret=6LcnrZcaAAAAADXBz4dkJOJFk8Ih2zewozKLyeGG&response=${response}`;
        let recaptcha_response = await formAPI.multi_call("");
        _this.$root.$emit("recaptcha_status", recaptcha_response);
      },
    },
  };
</script>
<style scoped></style>
