import Data from "@/js/core/data";

const Candidate_Server = "https://api.${region}.500apps.com/recruit";

class Candidate {
  async saveCandidate(domain_id, group_id, form_data) {
    let url = `${Candidate_Server}/${domain_id}/${group_id}/candidate`;
    let data = new Data(url);
    data.disablePCors();
    let candidate_response = await data.multi_call(form_data);
    return candidate_response;
  }
}
export default new Candidate();
