import Data from "@/js/core/data";

const Job_Details_Server = "https://api.${region}.500apps.com/recruit";

class JobDetails {
  getActiveJobs(domain_id, group_id) {
    let url = `${Job_Details_Server}/${domain_id}/${group_id}/jobs`;
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
  getFilteredJobs(domain_id, group_id, params) {
    let url = `${Job_Details_Server}/${domain_id}/${group_id}/jobs` + `${params}`;
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
  getJobDetailsById(domain_id, group_id, job_id) {
    let url = `${Job_Details_Server}/${domain_id}/${group_id}/jobs/${job_id}`;
    let job_data = new Data(url);
    job_data.disablePCors();
    return job_data.get();
  }
}
export default new JobDetails();
